.slider {
  position: relative;
  width: 100%;
  padding: 10px 0; }
  .slider .slider-input {
    width: 100%;
    appearance: none;
    background: #ddd;
    outline: none; }
    .slider .slider-input::-webkit-slider-thumb {
      appearance: none;
      background: #4caf50;
      cursor: pointer; }
    .slider .slider-input.single::-webkit-slider-thumb {
      height: 24px;
      width: 24px;
      border-radius: 50%; }
    .slider .slider-input.range::-webkit-slider-thumb {
      height: 32px;
      width: 32px;
      border-radius: 50%; }
  .slider .slider-values {
    text-align: center;
    margin-top: 10px;
    font-size: 14px; }
